export default (compareWithSync, low, high) => {
    let result = -1
    while (low <= high) {
        const mid = low + Math.floor((high - low + 1) / 2)
        const cmp = compareWithSync(mid)
        if (cmp < 0) {
            result = mid
            low = mid + 1
        } else if (cmp > 0) {
            high = mid - 1
        } else if (cmp === 0) {
            result = mid
            low = mid + 1
        }
    }
    return result
}
